.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 10;
  background: rgba(0, 0, 0, 0.75);
}

.modal {
  position: fixed;
  top: 10vh;
  left: 10%;
  width: 30%;
  z-index: 100;
  overflow: hidden;
}

.header {
  background: #4f005f;
  padding: 1rem;
}

.header h2 {
  margin: 0;
  color: white;
}

.content {
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.actions {
  padding: 1rem;
  display: flex;
  justify-content: space-evenly;
}
.modalImage {
  object-fit: contain;
  height: 50%;
  width: 50%;
}
@media (min-width: 768px) {
  .modal {
    left: calc(50% - 20rem);
    width: 40rem;
  }
}
@media (max-width: 728px) {
  .modal {
    left: calc(50% - 20rem);
    width: 90%;
  }
}
@media (max-width: 568px) {
  .modal {
    left: 15%;
    width: 20rem;
  }
}
